import date from 'moment-timezone'

let helpers = {}

helpers.twoDigits = (n) => {
  return n < 10 ? '0' + n : n
}

helpers.moment = (d = null) => {
  date.locale('fr')
  date.tz.setDefault('Europe/Paris')
  return d === null ? date.tz(moment(), 'Europe/Paris') : date.tz(d, 'Europe/Paris')
}

helpers.windowSmall = () => {
  return $(window).width() < 768
}

export default helpers




