<template>
  <section id="contact" class="pt-5 pb-0 position-relative">
    <div class="overlay-background"></div>
    <div class="row">

      <div class="col-6 text-center no-padding size-hover ml-auto mr-auto">
        <div class="section-title bounce-even">
          <h2>Contact</h2>
          <hr>
        </div>
      </div>

      <div class="col-sm-12 px-0">
        <div class="section-content bounce-odd">
          <div class="row">
            <div class="col-sm-7 bounce-odd mb-3" id="form-div">

              <form class="col-sm-12" id="contact-form" novalidate @submit.prevent="sendMsg()" method="post">

                <div class="row" data-aos="flip-up" data-aos-anchor="#contact-form">

                  <div class="col-sm-6 klk-form-group mb-3 w-100">
                    <input type="text" name="lastname" id="lastname" class="form-control"
                           v-model="mail.lastname"
                           :class="{'active': isActive(mail.lastname), 'is-invalid': hasError('lastname')}">
                    <label for="lastname">Votre nom</label>
                  </div>
                  <div class="col-sm-6 klk-form-group mb-3 w-100">
                    <input type="text" name="firstname" id="firstname" class="form-control"
                           v-model="mail.firstname"
                           :class="{'active': isActive(mail.firstname), 'is-invalid': hasError('firstname')}">
                    <label for="firstname">Votre prnom</label>
                  </div>

                </div>

                <div class="row" data-aos="flip-right" data-aos-anchor="#contact-form">
                  <div class="col-sm-12 klk-form-group mb-3 w-100">
                    <input type="text" name="email" id="email" class="form-control"
                           v-model="mail.email"
                           :class="{'active': isActive(mail.email), 'is-invalid': hasError('email')}">
                    <label for="email">Votre adresse email</label>
                  </div>
                </div>


                <div class="row" data-aos="flip-down" data-aos-anchor="#contact-form">
                  <div class="col-sm-12 klk-form-group mb-3 w-100">
                    <textarea id="message" v-model="mail.message"
                              :class="{'active': isActive(mail.message), 'is-invalid': hasError('message')}"
                              class="form-control pt-3" rows="10"></textarea>
                    <label for="message" class="m-0 mt-3">Votre message</label>
                  </div>
                </div>


                <div class="row" data-aos="fade-up" data-aos-anchor="#email">
                  <div class="col-sm-12 mb-2 w-100">
                    <button type="submit" class="btn btn-block bg-color-2 z-depth-0">Envoyer</button>
                  </div>
                </div>
              </form>

            </div>

            <div class="col-sm-5 bounce-even mb-3" id="infos-div" data-aos="zoom-in-left">

              <div class="row w-100">
                <div class="row w-100">
                  <div class="row w-100 mb-1">
                    <i class="fal fa-user-circle fa-6x color-2 mx-auto"></i>
                  </div>
                  <div class="row">
                    <span class="mx-auto">Sofiane Akbly</span>
                  </div>
                </div>
              </div>

              <hr class="w-75" id="experience-formation-hr"
                  style="border: 0; height: 2px; background-image: linear-gradient(to right, rgba(0, 0, 0, 0), #f0be47, rgba(0, 0, 0, 0));">

              <div class="row w-100">
                <ul class="address">
                  <li v-for="a in address" class="row w-100">
                    <div class="col-3 px-0 text-center"><i class="fa-2x" :class="a.icon"></i></div>
                    <div class="col-9"><a href="http://sofiane-akbly.890m.com">{{ a.text }}</a></div>
                  </li>
                </ul>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
    <!--<GmapMap :center="{lat:50.353920, lng:3.125690}" :zoom="15" map-type-id="terrain" class="w-100"-->
             <!--style="height: 300px"/>-->

    <!--<div id="google-map" data-latitude="50.35922" data-longitude="3.11525"></div>-->
  </section>
</template>

<script type="text/javascript">

  import axios from 'axios'
  import qs    from 'qs'

  export default {

    data() {
      return {
        sent   : null,
        nb     : 0,
        mail   : { lastname: null, firstname: null, email: null, message: null },
        errors : { lastname: null, firstname: null, email: null, message: null },
        address: [
          { icon: "fal fa-home", text: "59187 Dechy" },
          { icon: "fal fa-mobile", text: "+336.58.76.56.41" },
          { icon: "fal fa-at", text: "sofiane.akbly@gmail.com" }
        ]
      }
    },

    methods: {

      isActive(field) {
        return field !== null && field !== ''
      },

      hasError(field) {
        return this.errors[field] !== null && this.errors[field] === true
      },

      error_input(elt, msg = null) {
        elt.addClass('is-invalid');
        setTimeout(() => {
          $('.error-input').addClass('animate-fade');
        }, 100);
      },

      checkMail() {
        let ok = true
        Object.keys(this.mail).map((d) => {
          if (!this.mail[d] || this.mail[d].trim() === '') {
            ok = false
            this.errors[d] = true
          } else this.errors[d] = false
        })
        return ok
      },

      sendMsg() {
        $('.error-input').remove()

        if (this.checkMail() && this.nb++ < 3) {
          this.$store.commit('setFormLoad', true)
          let msg = `Contact :${this.mail.lastname} ${this.mail.firstname}, ${this.mail.email}\n\nContenu :${this.mail.message}`
          let header = `From: noreply@sakbly.com`
          axios.post('http://sofianeakbly-personal-test.000webhostapp.com/dashpi/dashboard/portfolio/mail', qs.stringify({
            msg,
            header
          })).then(() => {
            this.swal({
              type : 'success',
              title: 'Russi',
              html : "Le message a bien t envoy. <br>Je prendrai contact avec vous ds que possible."
            })
            this.$store.commit('setFormLoad', false)
            this.mail = { lastname: null, firstname: null, email: null, message: null }
          }, () => {
            this.swal({
              type : 'error',
              title: 'Oups',
              text : "Un incident technique est survenu, merci de bien vouloir ressayer."
            })
            this.$store.commit('setFormLoad', false)
          })
        }
      }
    },

  }
</script>

<style scoped>

  #contact {
    background-image: url(/static/img/bg/contact.jpg);
    height: 100%;
    background-position: 50%;
    background-repeat: no-repeat;
    color: #888;
    background-size: cover;
    background-size: 100% 100%;
    background-attachment: fixed
  }

  .section-content {
    z-index: 2;
  }

  :focus {
    outline: 0;
    box-shadow: none
  }

  textarea.form-control {
    height: inherit;
    font-size: 1rem;
    resize: none
  }

  #infos-div,
  #infos-div a {
    color: #fff;
    font-weight: lighter;
    text-decoration: none
  }

  #infos-div ul li {
    padding: 10px 15px;
    font-family: Tahoma, Geneva, sans-serif
  }
</style>



