<template>
  <section id="projet" class="pt-5">

    <div class="row">

      <div class="col-6 text-center no-padding size-hover ml-auto mr-auto">
        <div class="section-title bounce-odd"><h2>Projets</h2>
          <hr>
        </div>
      </div>

      <div class="row w-100 section-content bounce-even">

        <div class="col-sm-4 mr-auto ml-auto" v-for="(p, $index) in projets">

          <div class="projet-item" :data-aos="'flip-up'" :data-aos-delay="`${($index % 3) * 100}`">
            <div class="projet-item-image"><img :src="p.infos.images[0]" alt=""></div>
            <div class="projet-item-overlay d-flex justify-content-center align-items-center flex-column"
                 @click="setProject(p)">
              <div class="projet-item-overlay-content">
                <div class="projet-item-overlay-title"><h4>{{p.title}}</h4></div>
                <div class="projet-item-overlay-infos">
                  <small>Dvelopp pour : {{p.to}}</small>
                  <div class="more-infos">
                    <i class="fal fa-search-plus fa-3x"></i>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>

      </div>

    </div>

    <more-project :project="project"/>

  </section>
</template>

<script type="text/javascript">

  import MoreProject from './modals/MoreProject'


  export default {

    components: { MoreProject },
    methods   : {
      empty(i) {
        return (($(window).width() > 767) && (this.projets.length + i) % 4 !== 0)
      },

      setProject(project) {
        this.project = project
        $('#modalMoreProject').modal(project === null ? 'hide' : 'show')
      },
    },

    data() {
      return {
        project: null,
        projets: [{
          title: "Informations",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/iut/site_web1.png"), require("../../../assets/img/projects/iut/site_web2.png"), require("../../../assets/img/projects/iut/site_web3.png")],
            descriptions: [{
              content: `Le premier semestre a t une dcouverte du milieu informatique.  la fin de ce semestre, nous avons ralis un site web qui avait pour but de renseigner les tudiants et futurs tudiants sur les stages. De plus, nous listions les curriculums vitae d'tudiants de notre promotion. Cette dernire fonctionnalit tait destine aux entreprises qui recherchaient un stagiaire.`,
              link   : false
            }],
            date        : 'Dcembre 2014',
            languages   : ['HTML5', 'CSS3', 'Bootstrap']
          }
        }, {
          title: "Pacman",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/pacman/pacman1.gif"), require("../../../assets/img/projects/pacman/pacman2.png"), require("../../../assets/img/projects/pacman/pacman3.png")],
            descriptions: [{
              content: `Au semestre 2, nous avons eu l'occasion de raliser le clbre jeu "Pacman". Ce projet consistait encore une fois  nous faire travailler en quipe. Raliser un jeu aussi connu permet de prendre conscience que le domaine informatique est vraiment  l'origine de beaucoup de choses. La ralisation de ce projet a engendr acquisition de nouvelles connaissances en C++.`,
              link   : false
            }],
            date        : 'Juin 2015',
            languages   : ['C++', 'SMFL']
          }
        }, {
          title: "A la croise des chemins",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/labyrinthe/labyrinthe1.jpg"), require("../../../assets/img/projects/labyrinthe/labyrinthe2.png"), require("../../../assets/img/projects/labyrinthe/labyrinthe2.png")],
            descriptions: [{
              content: `Sans doute le projet le plus compliqu mais galement celui que j'ai le plus apprci lors de ma premire anne de DUT. En effet, tant plus jeune je jouais souvent au jeu "Le labyrinthe" et c'est pourquoi raliser ce mme jeu de manire informatique tait passionnant.`,
              link   : false
            }],
            date        : 'Juin 2015',
            languages   : ['JAVA']
          }
        }, {
          title: "Marathon web",
          to   : "IUT de Lens",
          infos: {
            images      : [require("../../../assets/img/projects/marathon/marathon_web.png"), require("../../../assets/img/projects/marathon/marathon_web1.png"), require("../../../assets/img/projects/marathon/marathon_web2.png")],
            descriptions: [{
              content: `Au semestre 3, nous avons t amens  travailler avec le dpartement MMI afin de mettre en commun les diffrentes comptences que nous avions acquises. Le mlange des dpartements a t effectu pour mettre en uvre un site de financement participatif. La complexit de ce projet est qu'il fallait analyser le projet, chercher des solutions et proposer un rsultat tout cela en deux petits jours, d'o le terme "Marathon web".`,
              link   : false
            }],
            date        : 'Dcembre 2015',
            languages   : ['PHP', 'HTML5', 'CSS3', 'Bootstrap']
          }
        }, {
          title: "Garage automobile SINOAS",
          to   : "Garage automobile SINOAS",
          infos: {
            images      : [require("../../../assets/img/projects/garage/garage1.png"), require("../../../assets/img/projects/garage/garage2.png"), require("../../../assets/img/projects/garage/garage3.png")],
            descriptions: [{
              content: `Voil ma premire ralisation pour un professionnel. En effet, une personne de mon entourage cherchait  raliser le site web de sa future socit. Dans un premier temps, je n'avais pas les comptences requises pour un tel projet puis au fur et  mesure de mon avance scolaire j'ai russi  lui produire un site adapt  ses besoins.`,
              link   : false
            }],
            date        : 'Janvier 2017',
            languages   : ['HTML5', 'CSS3', 'Bootstrap', 'Materialize', 'AngularJS', 'JQuery']
          }
        }, {
          title: "My phpmyadmin",
          to   : "ETNA Alternance",
          infos: {
            images      : [require("../../../assets/img/projects/mpma/mpma1.png"), require("../../../assets/img/projects/mpma/mpma2.png"), require("../../../assets/img/projects/mpma/mpma3.png")],
            descriptions: [{
              content: `Comme son nom l'indique, j'ai ralis ma propre version de "phpmyadmin" et je gre, grce  cet outil, toutes mes bases de donnes MySQL`,
              link   : false
            }],
            date        : 'Dcembre 2016',
            languages   : ['HTML5', 'CSS3', 'PHP', 'MySQL', 'Bootstrap', 'JQuery']
          }
        }, {
          title: "Yaghilaz",
          to   : "ETNA Alternance",
          infos: {
            images      : [require("../../../assets/img/projects/yaghilaz/yaghilaz1.png"), require("../../../assets/img/projects/yaghilaz/yaghilaz2.png"), require("../../../assets/img/projects/yaghilaz/yaghilaz3.png")],
            descriptions: [{
              content: `Nouvelle cole nouveau programme. Afin de se familiariser avec la programmation oriente objet PHP et l'architecture MVC, j'ai ralis un site recensant des recettes de cuisine.`,
              link   : false
            }],
            date        : 'Janvier 2017',
            languages   : ['HTML5', 'CSS3', 'PHP', 'MySQL', 'Bootstrap', 'JQuery']
          }
        }, {
          title: "Mean",
          to   : "ETNA Alternance",
          infos: {
            images      : [require("../../../assets/img/projects/mean/mean1.png"), require("../../../assets/img/projects/mean/mean2.png"), require("../../../assets/img/projects/mean/mean3.png")],
            descriptions: [{
              content: `Le dveloppement web est un milieu vaste. Ainsi, aprs avoir tudi le PHP sur un prcdent projet, j'ai approfondi mes connaissances sur l'AngularJS. Ce projet a t ralis avec la mise en place d'une API en NodeJS. Pour vraiment montrer la diffrence des autres projets, mme la base de donnes est diffrente. Jusque-l, la seule utilise tait MySQL, mais cette fois-ci c'est MongoDB.`,
              link   : false
            }],
            date        : 'Mai 2017',
            languages   : ['HTML5', 'CSS3', 'AngularJS', 'NodeJS', 'MongoDB', 'Materialize', 'JQuery']
          }
        }, {
          title: "Wovalen",
          to   : "Moi mme",
          infos: {
            images      : [require("../../../assets/img/projects/dashboard/dashboard1.png"), require("../../../assets/img/projects/dashboard/dashboard2.png"), require("../../../assets/img/projects/dashboard/dashboard3.png")],
            descriptions: [{
              content: `Afin de mettre  profit mon apprentissage sur un plan personnel, j'ai dcid de me crer un petit tableau de bord. Cette application regroupe les horaires que j'effectue sur mon temps de travail. Ou encore un planning contenant les jours de congs dont j'ai bnfici. De plus, elle regroupe les informations dont j'ai besoin pour mon cole puis enfin un outil de mail afin de communiqu plus facilement avec cette dernire.`,
              link   : false
            }],
            date        : 'Mai 2018',
            languages   : ['HTML5', 'CSS3', 'Vue.js', 'SlimPHP', 'MySQL', 'MDBootstrap', 'JQuery']
          }
        }]
      }
    }
  }</script>

<style scoped>

  #projet {
    background-image: url(/static/img/bg/projets.jpg);
    height: 100%;
    background-attachment: fixed;
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover
  }

  #projet .projet-item {
    position: relative;
    height: 25vw;
    cursor: pointer;
    margin-bottom: 1.5rem
  }

  #projet .projet-item .projet-item-image img {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%)
  }

  #projet .projet-item .projet-item-image,
  #projet .projet-item .projet-item-image img {
    overflow: hidden;
    height: 100%;
    width: 100%;
    transition: all .5s linear
  }

  #projet .projet-item:hover img {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
    -webkit-filter: inherit;
    filter: inherit
  }

  #projet .projet-item .projet-item-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: #fff;
    background-color: rgba(0, 0, 0, .7);
    height: 100%;
    width: 100%;
    padding: 0 10px
  }

  @-webkit-keyframes projetItemOverlay {
    0% {
      -webkit-transform: scale(0);
      transform: scale(0)
    }
    to {
      -webkit-transform: scale(1);
      transform: scale(1)
    }
  }

  @keyframes projetItemOverlay {
    0% {
      -webkit-transform: scale(0);
      transform: scale(0)
    }
    to {
      -webkit-transform: scale(1);
      transform: scale(1)
    }
  }

  .more-infos {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 5px;
    text-align: center
  }

  #projet .projet-item .projet-item-overlay .projet-item-overlay-title h4 {
    font-family: Aqua, sans-serif;
    text-align: center;
    font-size: 22px
  }

  #projet .projet-item .projet-item-overlay .projet-item-overlay-content {
    opacity: 0;
    transition: opacity 1s
  }

  #projet .projet-item:hover .projet-item-overlay .projet-item-overlay-content {
    opacity: 1
  }

  .img-project {
    height: 19vw
  }

  .img-project img {
    height: 19vw;
    width: 100%
  }

  @media (max-width: 767px) {
    #projet .projet-item {
      height: 300px !important;
    }
  }

</style>

