<template>
  <footer class="page-footer fat-grey pt-4">
    <div class="continer">
      <div class="row">
        <div class="col-sm-12">
          <div class="footer-logo">
            <a href="//sofiane-akbly.890m.com">
              <img src="../../../assets/logo.png" alt="Logo">
            </a>
          </div>
          <div class="social-link mt-2">
            <ul class="px-0">
              <li v-for="l in links" class="mx-1">
                <a class="fat-grey" target="_blank" :href="l.href">
                  <i class="fab icon" :class="`${l.icon} ${l.name}`"></i>
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="footer-copyright">
      <div class="fat-grey">
        <i class="fa fa-copyright"></i> 2017 Copyright
        <a href="//sofiane-akbly.890m.com" class="fat-grey">Sofiane Akbly</a>
        <br/>
        <span class="font-italic">Tous droits rservs.</span>
        <div class="link-no-opacity">
          <a class="no-opacity"
             href="http://sofiane-akbly.890m.com/#infos">Informations</a>
          <a class="no-opacity" href="http://sofiane-akbly.890m.com/#cv">Curriculum vitae</a>
          <a class="no-opacity" href="http://sofiane-akbly.890m.com/#projet">Projets</a>
          <a class="no-opacity" href="http://sofiane-akbly.890m.com/#contact">Contact</a>
        </div>
      </div>
    </div>
  </footer>
</template>

<script type="text/javascript">
  export default {
    data() {
      return {
        links: [
          { href: "//twitter.com/", icon: "fa-twitter-square", name: "twitter" },
          { href: "//facebook.com/", icon: "fa-facebook-square", name: "facebook" },
          { href: "//www.linkedin.com/in/sofiane-akbly/", icon: "fa-linkedin", name: "linkedin" },
          { href: "//plus.google.com/", icon: "fa-google-plus-square", name: "google" },
          { href: "//youtube.com/", icon: "fa-youtube-square", name: "youtube" }
        ]
      }
    }
  }
</script>

<style scoped>


  footer {
    width: 100%
  }

  footer.page-footer {
    background-color: #fff
  }

  footer .footer-logo img,
  footer .social-link ul {
    display: block;
    margin-right: auto;
    margin-left: auto
  }

  footer .social-link ul {
    text-align: center
  }

  footer .social-link li {
    display: inline;
    font-size: 3em
  }

  footer .social-link li a .icon {
    transform-style: preserve-3d;
    -webkit-transform-style: preserve-3d;
    transition: all 1s ease-in-out;
    -webkit-transition: all 1s ease-in-out
  }

  footer .social-link li a:hover,
  footer .social-link li a:hover .icon {
    text-decoration: none;
    transform: rotateX(1turn);
    -webkit-transform: rotateX(1turn)
  }

  footer .social-link .twitter {
    color: #00aced
  }

  footer .social-link .facebook {
    color: #3b5998
  }

  footer .social-link .linkedin {
    color: #007bb6
  }

  footer .social-link .google {
    color: #dd4b39
  }

  footer .social-link .youtube {
    color: #b00
  }

  footer.page-footer .footer-copyright {
    background-color: #eee;
    text-align: right
  }

  .fat-grey {
    color: #888;
    width: 100%
  }

  .no-opacity {
    opacity: 0 !important;
  }

</style>




