<template>
  <!-- Full Height Modal Right -->
  <div class="modal fade right" id="modalMoreProject" tabindex="-1" role="dialog" aria-labelledby="myModalLabel"
       aria-hidden="true">

    <!-- Add class .modal-full-height and then add class .modal-right (or other classes from list above) to set a position to the modal -->
    <div class="modal-dialog modal-full-height modal-right" :class="{'w-50': !helpers.windowSmall()}" role="document">


      <div class="modal-content color-1 rounded-0 z-depth-1-half">
        <div class="modal-header border-0">
          <button type="button" class="close w-100 text-center mx-0" data-dismiss="modal" aria-label="Close">
            <i class="fal fa-times fa-2x small color-2"></i>
          </button>


        </div>
        <div class="modal-body">

          <div class="project-more-content" v-if="project">

            <div class="row mb-2">
              <div class="col-sm-4 mb-2" v-for="image in project.infos.images">
                <div class="img-project"><img :src="image" class="img-fluid" :alt="image"/></div>
              </div>
            </div>

            <div class="row">
              <div class="col-12 description-project text-justify">
                <div class="row"><h5><i class="far fa-info-square color-2"></i> {{project.title}}</h5>
                  <p v-for="description in project.infos.descriptions">
                    <span v-if="description.link">
                      <a :href="description.href">{{description.content}}</a>
                    </span>
                    <span v-else-if="!description.link">{{description.content}}</span>
                  </p>
                </div>
              </div>

              <div class="col-12 px-0">
                <div class="row">
                  <div class="col-12 col-md-6 details-project w-100">
                    <div class="row"><h5><i class="fal fa-eye color-2"></i> Dtails du projet</h5></div>
                    <div class="row">
                      <div class="col-10 mx-auto">
                        <ul>
                          <li><i class="fal fa-star-christmas color-2"></i> Client : {{project.to}}</li>
                          <li><i class="fal fa-star-christmas color-2"></i> Date : {{project.infos.date}}</li>
                        </ul>
                      </div>
                    </div>
                  </div>
                  <div class="col-12 col-md-6 languages-project w-100">
                    <div class="row"><h5><i class="far fa-language color-2"></i> Langages</h5></div>
                    <div class="row">
                      <div class="col-10 mx-auto">
                        <ul>
                          <li v-for="l in project.infos.languages"><i class="fal fa-star-christmas color-2"></i> {{l}}
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

            </div>
          </div>

        </div>
      </div>
    </div>
  </div>
  <!-- Full Height Modal Right -->
</template>

<script>
  export default {
    name: "MoreProject",

    props: ['project'],

    $watch: {
      project(val) {
        console.log(val)
      }
    }

  }
</script>

<style scoped>

  .modal-dialog {
    max-width: 100% !important;
  }

  .img-project {
    height: 19vw
  }

  .img-project img {
    height: 15vw;
    width: 100%
  }

  ul {
    list-style-type: none !important;
    padding-left: 0 !important;
  }

  @media (max-width: 767px) {
    .img-project, .img-project img {
      height: 225px !important;
    }
  }

</style>



